import whatsappIcon from "./whatsapp.svg";
import instIcon from "./instagram.svg";
import playmarketIcon from "./playmarket.svg";
import appstoreIcon from "./appstore.svg";
import styles from "./SocialLinkButton.module.scss";

function SocialLinkButton({ name, href }) {
  const logo = () => {
    switch (name) {
      case "instagram":
        return instIcon;

      case "whatsapp":
        return whatsappIcon;

      case "playmarket":
        return playmarketIcon;

      case "appstore":
        return appstoreIcon;

      default:
        return null;
    }
  };

  return (
    <a href={href} className={styles.button}>
      <img src={logo()} alt="social-media" />
    </a>
  );
}

export default SocialLinkButton;
